taticClass:"mb-0"},[_vm._v("Consideraciones en relación con los sujetos y objetivos de la EpS grupal por curso de vida, la Resolución 3280 de 2018, así como los documentos de la Política de Infancia y Adolescencia reseñan algunas características de los procesos de desarrollo y salud por momentos de curso de vida, que se consideran clave para los procesos de educación para la salud, como se muestran en la siguiente tabla:")])])])])])])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row justify-content-center mb-5",attrs:{"data-aos":"fade-up"}},[_c('div',{staticClass:"col-lg-10"},[_c('div',{staticClass:"titulo-sexto color-acento-contenido"},[_c('p',{staticClass:"mb-0"},[_c('b',[_vm._v("Tabla 1")]),_vm._v(". Procesos de EpS por momento del curso de vida")])]),_c('div',{staticClass:"tabla-a color-acento-contenido"},[_c('table',[_c('caption',{staticClass:"mt-3"},[_vm._v("Nota. Adaptado a partir de lo definido en la Resolución 3280 de 2018.")]),_c('thead',[_c('tr',[_c('th',[_vm._v("Momento de curso de vida")]),_c('th',[_vm._v("Definición y elementos a considerar")]),_c('th',[_vm._v("Objetivos de los procesos de EpS por momento del curso de vida")])])]),_c('tbody',[_c('tr',[_c('td',{staticStyle:{"vertical-align":"initial"}},[_vm._v("Primera infancia")]),_c('td',{staticStyle:{"background-color":"#F0F3FA"}},[_vm._v("La primera infancia cobija desde la gestación hasta los 5 años de vida. Las niñas y los niños de primera infancia son seres sociales, diversos y singulares, que interactúan con el mundo y despliegan todas sus potencialidades, movidos por su deseo de expresarse, conocer, explorar, experimentar, descubrir, moverse y comprender quiénes son, de qué son capaces, cómo funcionan las cosas que les rodean, cómo lo pueden transformar, cómo se convive con otros seres humanos, cómo se comunican. En medio de estas interacciones tiene lugar su desarrollo. Desde esta comprensión es fundamental el apoyo y promoción de este proceso de desarrollo a nivel físico, social, afectivo, del lenguaje y del pensamiento; a partir de un cuidado cariñoso y sensible a las necesidades del niño o niña, conociendo y acompañando el proceso de desarrollo y protegiéndolo ante los riesgos.")]),_c('td',{staticStyle:{"vertical-align":"initial"},attrs:{"rowspan":"2"}},[_vm._v("Acompañar a padres y cuidadores en el fortalecimiento de sus capacidades para la crianza, el cuidado de la salud y protección que promuevan la salud y el desarrollo humano integral de niños, mediante un proceso de construcción colectiva de saberes y prácticas para el cuidado y protección propios, de sus familias y de los entornos.")])]),_c('tr',[_c('td',{staticStyle:{"vertical-align":"initial"}},[_vm._v("Infancia")]),_c('td',{staticStyle:{"background-color":"#F0F3FA"}},[_vm._v("Comprende entre los 6 y los 11 años y supone la experiencia de consolidación del proceso de desarrollo que viene configurándose desde la primera infancia y materializa el tránsito a nuevos aprendizajes que resultan de mayores complejidades en el uso o desarrollo de capacidades, habilidades y potencialidades, lo cual redunda en logros progresivos de autonomía e independencia, unido a mayores interacciones y riqueza relacional. Los niños y niñas entre los 6 y los 11 años de edad tienen muchas potencialidades para aprender, cada vez se hacen más conscientes de sus capacidades, por lo que van aumentando su interacción social fuera de la familia. Suelen disfrutar de juegos de reglas y competencia, logran resolver problemas cotidianos de manera independiente y aprenden imitando lo que ven en las personas de su entorno. Este es un momento muy importante para incentivarlos a adoptar pasatiempos que les ayuden a estar más saludables y felices.")])]),_c('tr',[_c('td',{staticStyle:{"vertical-align":"initial"}},[_vm._v("Adolescencia")]),_c('td',{staticStyle:{"background-color":"#F0F3FA"}},[_vm._v("La adolescencia se considera el momento vital entre los 12 y los 17 años, es considerada un momento vital caracterizado por profundas transformaciones, cuyo dinamismo trae consigo crisis, conflictos y contradicciones, necesarias para definir la identidad."),_c('br'),_c('br'),_vm._v("Como parte del proceso de construcción identitaria cada adolescente se replantea su definición personal y social, a través de procesos de diferenciación del grupo familiar y de búsqueda de pertenencia y de sentido de vida. En este proceso de construcción y logro de autonomía tienen un papel fundamental la capacidad de reflexión y de toma de decisiones, así como los entornos, la relación con sus pares y su contexto. "),_c('br'),_c('br'),_vm._v("Los adolescentes están en un momento de su vida en el que se presentan profundas transformaciones biológicas, psicológicas y sociales, muchas de ellas pueden generar crisis, conflictos y contradicciones; pero esencialmente son cambios positivos, pues no es solamente un periodo de adaptación a cambios corporales, sino una fase de grandes determinaciones hacia una mayor independencia psicológica y social, siendo un momento clave en el proceso de socialización con importantes aprendizajes que permiten fortalecer las interrelaciones personales para distinguirse de otros y adaptarse a la sociedad")]),_c('td',{staticStyle:{"vertical-align":"initial"}},[_vm._v("a. Reforzar las potencialidades de todos los adolescentes, en lo relacionado con su capacidad de agencia, autonomía y construcción de identidad."),_c('br'),_c('br'),_vm._v("b. Acompañar al adolescente en el reconocimiento, comprensión y reflexión sobre los procesos emergentes del momento vital relacionados con los cambios fisiológicos, psicológicos, proyecto de vida, la sana convivencia, la sexualidad y la reproducción. "),_c('br'),_c('br'),_vm._v("c. Desarrollar o potenciar capacidades para el ejercicio de sus derechos, con el fin que estos procesos contribuyan a su desarrollo integral y su calidad de vida presente y futura, así como para el cuidado de su salud. "),_c('br'),_c('br'),_vm._v("d. Desarrollar capacidades en los padres y cuidadores que les permitan comprender y gestionar los cambios que suceden en la adolescencia, encontrar formas de renegociar las relaciones parento- filiales, resolver los problemas de forma productiva, apoyar el desarrollo de la autonomía e identidad y mantener un vínculo positivo y de apoyo emocional.")])]),_c('tr',[_c('td',{staticStyle:{"vertical-align":"initial"}},[_vm._v("Juventud")]),_c('td',{staticStyle:{"background-color":"#F0F3FA"}},[_vm._v("Comprende la trayectoria entre los 18 y los 28 años, se comprende como un momento de afirmación en el que el escenario social se convierte en el nicho para concretar y estructurar los intereses y propósitos que se han venido vislumbrando y potenciando desde la niñez y que ahora entran en interacción con las condiciones y oportunidades del contexto, adentrándose en la experiencia de asumir la construcción de una vida independiente que se consolida en la edad adulta. Los y las jóvenes se caracterizan por su entusiasmo y energía permanente, es el momento de consolidación de la autonomía intelectual, física, moral, económica, social y cultural, lo que se evidencia en actitudes de seguridad, poder y dominio. La juventud impacta a una generación completa y se expresa con vigor en todos los campos: familiar, educativo, laboral, etc.")]),_c('td',{staticStyle:{"vertical-align":"initial"}},[_vm._v("Desarrollar capacidades en las y los jóvenes para transformar saberes y prácticas en pro de una vida saludable, relacionadas con el ejercicio de la sexualidad y reproducción libre e informada, la sana convivencia, la educación para el trabajo y el desarrollo humano en los diferentes escenarios durante el curso de vida, la capacidad política y la autonomía para agenciarse y relacionarse con otros/as.")])]),_c('tr',[_c('td',{staticStyle:{"vertical-align":"initial"}},[_vm._v("Adultez")]),_c('td',{staticStyle:{"background-color":"#F0F3FA"}},[_vm._v("Comprende el momento de vida que transcurre entre los 29 a 59 años. Se configura en un proceso dinámico, en donde continúa el desarrollo y las personas son capaces de establecer metas y poner en marcha los recursos necesarios para alcanzarlas, configurando sus trayectorias evolutivas personales y aprovechando las oportunidades disponibles en sus contextos. "),_c('br'),_c('br'),_vm._v("En este sentido, es importante subrayar que los patrones de desarrollo adulto no se configuran en algo novedoso, sino en la consolidación de los aprendizajes previos y un perfilamiento cada vez más claro de la individualidad.")]),_c('td',{staticStyle:{"vertical-align":"initial"}},[_vm._v("Desarrollar capacidades con los adultos para la promoción y mantenimiento de su salud, con la finalidad de aportar al goce efectivo del derecho a la salud, mediante un proceso de construcción colectiva de saberes y prácticas.")])]),_c('tr',[_c('td',{staticStyle:{"vertical-align":"initial"}},[_vm._v("Vejez")]),_c('td',{staticStyle:{"background-color":"#F0F3FA"}},[_vm._v("Se considera persona mayor a toda persona desde los 60 años. En este momento de la vida se disfruta a partir de los aprendizajes y experiencia acumulada. Existen diversos enfoques para comprender el proceso de envejecimiento y las características de la vejez; sin embargo, estos pueden vivirse desde las pérdidas o desde la plenitud, de acuerdo con los efectos acumulados a lo largo de la vida, la confluencia de las oportunidades y los recursos, tanto individuales como generacionales, que afectan el continuo de la vida de cada persona, así como su condición y posición social y su conjugación con la edad, el género, la clase social y el origen étnico, que representan ciertas diferencias en el acceso y disfrute de dichos recursos y oportunidades.")]),_c('td',[_vm._v("a. Desarrollar capacidades con las personas adultas mayores para la promoción y mantenimiento de su salud, con la finalidad de aportar al goce efectivo del derecho a la salud, mediante un proceso d