. Preguntas de aprendizaje de acuerdo con la temática, se proponen al menos diez preguntas de aprendizaje.")]),_c('p',[_vm._v("Por ejemplo, ¿cuáles actividades son propias del papel productivo, reproductivo y comunitario de los hombres y las mujeres tanto en la familia como en la comunidad?, ¿qué acciones realizan para garantizar la salud sexual y reproductiva? ")]),_c('p',[_vm._v("3. Contenido temático, con introducción y nociones clave para el cuidado de la salud de acuerdo con el tema abordado. Se propone tener lista unas diez ideas clave. Para esto es necesario un conocimiento profundo de la temática que permite agenciar el diálogo con los participantes en la sesión:")]),_c('p',[_vm._v("Por ejemplo, construcción social del género, asignación de roles y actividades específicas en función de las capacidades para reproducir la vida y el bienestar, con un enfoque visto desde las libertades y la agencia de los sujetos.")]),_c('p',[_vm._v("4. Ideas fuerza (el mensaje que se espera sea apropiado por los sujetos), se sugiere al menos diez ideas fuerza.")]),_c('p',[_vm._v("Por ejemplo, “la sexualidad va más allá de tener relaciones sexuales, parte del reconocimiento y autoidentificación de las emociones, características, conducta y prácticas de los individuos, las cuales se vivencian en diferentes momentos del curso de la vida, determinados por las particularidades del territorio donde se desarrollan las personas, las tradiciones y costumbres de los pueblos y a las experiencias propias”. ")]),_c('p',[_vm._v("5- Expresiones de las prácticas en el territorio (desde la interculturalidad, una práctica en el territorio que funcione y sea segura, o una práctica que requiera transformar para la seguridad de los sujetos), es decir, identificar las prácticas protectoras y las prácticas que no se deben fomentar. De acuerdo con el diálogo de saberes se requiere establecer las prácticas y definir entre todos si es una práctica que se puede fomentar o si hay que eliminarla, ya que no